@font-face {
	font-family: "SC Prosper Sans Bold";
	src: url( "../src/fonts/SCProsperSans-Bold.eot" );
	src:
		url( "../src/fonts/SCProsperSans-Bold.eot?#iefix" ) format( "embedded-opentype" ),
		url( "../src/fonts/SCProsperSans-Bold.woff2" ) format( "woff2" ),
		url( "../src/fonts/SCProsperSans-Bold.woff" ) format( "woff" ),
